*[class*=-label-] {
  cursor: default;
}
*[class*=-label-] * {
  cursor: default;
}

.m-list-optionlist label {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.overall-progress.complex .a-label-section-title {
  display: block;
  margin: 0 0 8px 0;
  font-size: 24px;
  text-align: right;
}

@media screen and (max-width: 1024px) {
  .overall-progress.complex .a-label-section-title {
    margin-bottom: 4px;
    font-size: 18px;
  }
}
th .a-label-option {
  color: #212C4C;
}

.a-label-option {
  color: #455B97;
  padding: 0;
  margin: 0;
}

.a-label-iteration {
  font-family: "Karla", sans-serif;
  color: #455B97;
}

.a-label-caption {
  font-family: "Merriweather", serif;
  font-weight: normal;
  color: #212C4C;
}

.a-label-total {
  display: inline-block;
  padding: 10px 16px;
  border: 1px solid transparent;
  margin: 0 0 0.5em 0;
  height: 19px;
  text-align: right;
  box-sizing: content-box;
}

.a-label-total-grand,
.a-label-total-column,
.a-label-total-row {
  color: #212C4C;
}

.a-label-total-column,
.a-label-total-grand {
  margin-bottom: 0;
}

.a-label-error {
  color: #DA2911;
}

.o-question-instruction {
  color: #0063DE;
}
.o-question-instruction p {
  margin: 0.5em 0;
}

.o-question-error *[class*=-label-],
.o-question-warning *[class*=-label-],
.o-question-success *[class*=-label-] {
  display: inline-block;
  padding: 1em 1em 1em 2.5em;
  border-radius: 10px;
  color: #FFFFFF;
  margin-bottom: 1em;
  background: no-repeat 1em;
  background-size: 17px;
}

.o-question-error *[class*=-label-] {
  background-image: url("../images/error.png");
  background-color: #DA2911;
}

.o-question-warning *[class*=-label-] {
  background-image: url("../images/warning.png");
  background-color: #E8BE5D;
}

.o-question-success *[class*=-label-] {
  background-image: url("../images/success.png");
  background-color: #469B9B;
}

.focused * {
  outline: none;
}
.focused .a-label-postlabel {
  border: 1px solid #455B97 !important;
  border-left-color: #8797C8 !important;
}
.focused .a-label-prelabel {
  border: 1px solid #455B97 !important;
  border-right-color: #8797C8 !important;
}

.m-structure-cell .read-only .a-label-postlabel,
.m-structure-cell .read-only .a-label-prelabel {
  background: transparent;
}
.m-structure-cell .a-label-postlabel {
  display: inline-block;
  border: 1px transparent;
  position: relative;
  left: -0.5em;
  padding: 10px 16px;
  white-space: nowrap;
  box-sizing: content-box;
  height: 19px;
}
.m-structure-cell .a-label-prelabel {
  display: inline-block;
  border: 1px transparent;
  padding: 10px 16px;
  white-space: nowrap;
}

.m-input-singlelineedit .a-label-postlabel {
  display: inline-block;
  border: 1px solid #8797C8;
  border-radius: 0 10px 10px 0;
  position: relative;
  left: -0.5em;
  background-color: #FAFAFA;
  padding: 10px 16px;
  color: #A0ABCC;
  white-space: nowrap;
  box-sizing: content-box;
  height: 19px;
}
.m-input-singlelineedit .a-label-prelabel {
  display: inline-block;
  border: 1px solid #8797C8;
  border-radius: 10px 0 0 10px;
  background-color: #FAFAFA;
  padding: 10px 16px;
  color: #A0ABCC;
  white-space: nowrap;
}
.m-input-singlelineedit.read-only .a-label-prelabel,
.m-input-singlelineedit.read-only .a-label-postlabel {
  border: transparent !important;
}

.o-question-hnumberslider-control .a-label-prelabel {
  position: absolute;
  left: 0;
  bottom: -0.8em;
  color: #455B97;
}
.o-question-hnumberslider-control .a-label-postlabel {
  position: absolute;
  right: 0;
  bottom: -0.8em;
  color: #455B97;
}
.o-question-hnumberslider-control .a-label-thumbvalue {
  box-sizing: border-box;
  color: #212C4C;
  position: absolute;
  top: -1.1em;
  visibility: hidden;
  transform: translateX(-50%);
}
.o-question-hnumberslider-control.active.has-value.has-thumb-value .a-label-thumbvalue {
  visibility: visible;
}
.o-question-hnumberslider-control .m-style-slidermarks {
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  width: calc(100% - 12px);
  left: 6px;
  height: 16px;
  top: 16px;
  border-radius: 16px;
}
.o-question-hnumberslider-control .m-label-ticklabels {
  position: relative;
  top: -0.5em;
  left: 6px;
  box-sizing: border-box;
  width: calc(100% - 12px);
  display: flex;
  justify-content: space-between;
  color: #8797C8;
}
.o-question-hnumberslider-control .m-label-ticklabels > span {
  text-align: center;
  flex: 0 1 40px;
  display: block;
}

.m-option-base input:checked + label > .a-label-option {
  color: #449B9B;
}
.m-option-base input:disabled + label > .a-label-option {
  color: #9CADCB;
}
.m-option-base .a-label-option {
  display: inline-block;
  padding-left: 1.5em;
}

.m-list-optionlist .a-label-option {
  padding-bottom: 0.5em;
}
.m-list-optionlist .m-option-base:last-child .a-label-option {
  padding-bottom: 0;
}

.m-structure-cell .m-option-base .a-label-option {
  padding-left: 0.2em;
}

.m-list-optionlist .a-label-option {
  width: calc(100% - 1.5em);
}

div.o-question-list:not(.display-icons) .m-list-optionlist .a-label-option {
  padding-left: 3px;
  width: calc(100% - 3px);
}